
* {
  box-sizing: border-box;
  --main-accent: #060b35;
  --secondary-accent: #616070;
  --blue-accent: #00acdc;
  --card-border-color: #f3f3f3;
  --background-color: #fff;
  --button-accent-color: #fff;
}

body,
p,
img,
article,
form,
button,
a,
ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
}
